export default {
  data: () => ({
    total: 0,
    items: [],
    search: null,
    page: 1,
    limit: 10,
    order: "ASC",
    orderBy: "id",
    dialog: {
      status: false,
      id: null,
      name: null,
    },
    service: null,
    loading: false,
    onRoute: false,
    pagination: {},
  }),

  watch: {
    page() {
      if (!this.onRoute) {
        this.fetch();
      }
    },
    limit() {
      if (!this.onRoute) {
        this.page = 1;
        this.fetch();
      }
    },
    async $route() {
      this.onRoute = true;
      this.page = 1;
      this.limit = 10;
      await this.getService();
      await this.fetch();
      this.onRoute = false;
    },
  },

  computed: {
    headers: function() {
      return this.$store.getters[this.$route.meta.module + "/headers"];
    },
  },

  methods: {
    clearSearch() {
      this.search = null;
      this.page = 1;
      this.fetch();
    },

    submitSearch() {
      this.page = 1;
      this.fetch();
    },

    async fetch() {
      const self = this;
      self.loading = true;
      self.search = self.search === "" ? null : self.search;
      const params = {
        page: self.page,
        limit: self.limit,
        order: self.order,
        orderBy: self.orderBy,
        search: self.search,
      };

      await self.service.getAll(params).then(function(response) {
        if (response.data) {
          self.items = response.data.Results;
          self.total = Math.ceil(response.data.Total / params.limit);
        }
      });

      self.loading = false;
    },

    async fetchItem(context, params) {
      const self = this;
      self.item = null;
      await self.service.getId(params.id).then((response) => {
        if (
          response.data &&
          response.data.Results &&
          response.data.Results[0]
        ) {
          self.item = response.data.Results[0];
        }
      });
    },

    sortItems(items, index, isDesc) {
      const self = this;
      if (!index) {
        if (self.orderBy !== "ID" || self.order !== "ASC") {
          self.orderBy = "ID";
          self.order = "ASC";
          self.fetch();
        }
        return items;
      }
      const header = self.headers.find(
        (items) => items.value === (index ? index[0] : index),
      );

      if (header) {
        const order = isDesc[0] ? "DESC" : "ASC";
        if (self.orderBy !== header.value || self.order !== order) {
          self.orderBy = header.value;
          self.order = isDesc[0] ? "DESC" : "ASC";
          self.fetch();
        }
      }

      return items;
    },

    add(data) {
      const self = this;
      return self.service.add(data).then((response) => {
        if (response.data) {
          return response.data;
        }
        return false;
      });
    },

    edit(data) {
      const self = this;
      return self.service.edit(data).then((response) => {
        if (response.data) {
          return true;
        }
        return false;
      });
    },

    delete(itemId) {
      const self = this;

      return self.service.delete(itemId).then(function(response) {
        if (response.data) {
          self.items = self.items.filter((item) => item.id !== response.data);
          return true;
        }
        return false;
      });
    },

    setDialog({ id, name, status }) {
      this.dialog = {
        status: status,
        id: id,
        name: name,
      };
    },

    confirmDeletion(id) {
      const self = this;
      self.setDialog({});
      self.delete(id).then((response) => {
        if (response) {
          // self.$noty.success("Deletado");
          self.fetch();
        }
      });
    },

    async getService() {
      this.service = await import(
        `@/api/services/${this.$route.meta.module}`
      ).then((module) => {
        return module.default;
      });
    },
  },

  async created() {
    await this.getService();
    this.fetch();
  },
};
